<template>
  <div>
    <v-navigation-drawer v-model="drawer" color="primary" dark temporary app>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="title mt-5" align="center">
            Recipe
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list>
        <v-list-group no-action sub-group :value="true" color="secondary">
          <template v-slot:activator>
            <v-list-item-content color="red">
              <v-list-item-title>メニュー</v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item-group v-model="retindex">
            <v-list-item
              v-for="(menu, i) in menus"
              :key="i"
              link
              :href="menu.href"
            >
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-list-item-title class="ml-10" v-on="on">{{
                    menu.text
                  }}</v-list-item-title>
                </template>
                <span>{{ menu.message }}</span>
              </v-tooltip>
            </v-list-item>
          </v-list-item-group>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar app color="primary" dark>
      <v-app-bar-nav-icon @click="sidebarStatus"> </v-app-bar-nav-icon>
      <v-toolbar-title>Recipe</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-menu offset-y v-if="account.email !== ''">
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>
        <v-list color="primary" dark>
          <v-list-item>
            <v-list-item-title>
              <v-icon>mdi-account</v-icon> {{ account.username }}<br />
              <v-icon>mdi-email</v-icon> {{ account.email }}<br />
              <v-row>
                <v-col cols="12" xs="8">
                  <v-img width="20%" :src="account.photoURL" />
                </v-col>
                <v-col cols="12" xs="4">
                  <router-link to="logout">
                    <v-btn> Logout</v-btn>
                  </router-link>
                </v-col>
              </v-row>
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
  </div>
</template>
<script>
export default {
  props: {
    account: Object
  },
  data() {
    return {
      drawer: false,
      model: {
        nav: "x"
      },
      menus: [
        {
          href: "home",
          text: "ホーム",
          message: "ホーム画面"
        },
        {
          href: "list",
          text: "編集",
          message: "編集するための一覧を開きます"
        },
        {
          href: "logout",
          text: "ログアウト",
          message: "ログアウトします"
        }
      ]
    };
  },
  methods: {
    sidebarStatus() {
      this.drawer = !this.drawer;
    }
  },
  computed: {
    retindex() {
      let m_i = 0;
      for (let men of this.menus) {
        if (men.href === this.$route.name) {
          return m_i;
        }
        m_i++;
      }
      return 0;
    }
  }
};
</script>
<style>
.cusmenu {
  color: red;
}
