<script>
import firebase from "firebase";
export default {
  created() {
    let that = this;
    firebase.auth().onAuthStateChanged(function(user) {
      if (user) {
        that.account.username = user.displayName;
        that.account.email = user.email;
        that.account.photoURL = user.photoURL;
      } else if (that.$route.name === "login") {
        //console.log(user);
        // User is signed in.
      } else {
        that.$router.push("login");
        // No user is signed in.
      }
    });
  },
  methods: {},
  data() {
    return {
      account: {
        username: "",
        email: "",
        photoURL: ""
      }
    };
  }
};
</script>
